import * as React from "react"
import { connect } from "react-redux"
import { calcEmmissionIn2030, calcReduction, kTCO2e } from "../utils"
import DataCard from "./DataCard"


const InputRange = (props) => {
  const country = props.countryName
  const currentGoal = props.goalIn2030
  const baseEmissions = props.GHG1990
  const currentEmissions = props.GHG2018
  const { changeData } = props
  const [value, setValue] = React.useState(currentGoal ? currentGoal : 55);
  const [open, setOpen] = React.useState(false);
  
  React.useEffect(()=>{
    changeData([parseInt(value), calcEmmissionIn2030(baseEmissions, value)], props.stateID)
  }, [value, baseEmissions, changeData, props.stateID])
  
  const handleChange = (e) => {
    setValue(e.target.value)
  }
 //<input type="input" id={country} name={country} min="0" max="100" value={value} onChange={e=>handleChange(e)}/>
  return (
    <>
    <div className="input-element">
      <label htmlFor={country}>
        {country} / {-1*value}%
      </label>
      <input type="range" id={country} name={country} min="0" max="100" defaultValue={value} onChange={e=>handleChange(e)}/>
      
      <button onClick={e=>{e.preventDefault(); setOpen(!open)}}>{!open ? "more" : "less"}</button>
      </div>
      <div className={open ? "data-cards-container" : "hide"}>
        <DataCard title="Target emmissions in 2030" value={calcEmmissionIn2030(baseEmissions, value)} unit={kTCO2e}/>
        <DataCard title="Target reduction in 2030" value={`${-1*value}%`} />
        <DataCard title="Emmissions in 1990" value={baseEmissions} unit={kTCO2e}/>
        <DataCard title="Emmissions in 2018" value={currentEmissions} unit={kTCO2e}/>
        <DataCard title="Change between 1990 and 2018" value={`${-1*calcReduction(baseEmissions, currentEmissions)}%`} />
      </div>
      </>   
  )
}

const mapStateToProps = (state) => {
  return {
    store: state
}
}

const mapDispatchToProps = (dispatch) => {
  return {
    changeData: (data, id) => dispatch({ type: 'CHANGE_DATA', data: data, id: id}),
}
}

export default connect(mapStateToProps, mapDispatchToProps)(InputRange)