import * as React from "react"
import { connect } from "react-redux";
import { EUData } from "../data";
import { calcReduction, calcSumOfEmissions, kTCO2e } from "../utils";
import DataCard from "./DataCard";
import { LineChart, Line, XAxis, YAxis, ResponsiveContainer, Tooltip } from 'recharts';


const SumDisplay = (props) => {
  const { changeSum } = props
  const { GHG1990 } = EUData
    React.useEffect(()=>{
        changeSum(calcSumOfEmissions(props.countries))
    })
    const data = [
      {yr: 1990, ghg: GHG1990/1000}, 
      {yr: 1991, ghg: 5013504.49/1000}, 
      {yr: 1992, ghg: 4906708.96/1000}, 
      {yr: 1993, ghg: 4760075.77/1000}, 
      {yr: 1994, ghg: 4669402.12/1000}, 
      {yr: 1995, ghg: 4729079.89/1000}, 
      {yr: 1996, ghg: 4829519.17/1000}, 
      {yr: 1997, ghg: 4773010.47/1000}, 
      {yr: 1998, ghg: 4743073.42/1000}, 
      {yr: 1999, ghg: 4667485.36/1000},
      {yr: 2000, ghg: 4671478.64/1000}, 
      {yr: 2001, ghg: 4721241.24/1000}, 
      {yr: 2002, ghg: 4711336.56/1000}, 
      {yr: 2003, ghg: 4799214.74/1000}, 
      {yr: 2004, ghg: 4818507.12/1000}, 
      {yr: 2005, ghg: 4800544.44/1000}, 
      {yr: 2006, ghg: 4810389.06/1000}, 
      {yr: 2007, ghg: 4785962.95/1000}, 
      {yr: 2008, ghg: 4689556.21/1000}, 
      {yr: 2009, ghg: 4347140.71/1000},
      {yr: 2010, ghg: 4438152.34/1000}, 
      {yr: 2011, ghg: 4327962.86/1000}, 
      {yr: 2012, ghg: 4237005.15/1000}, 
      {yr: 2013, ghg: 4145740.61/1000}, 
      {yr: 2014, ghg: 4007837.28/1000}, 
      {yr: 2015, ghg: 4063842.35/1000}, 
      {yr: 2016, ghg: 4075123.58/1000}, 
      {yr: 2017, ghg: 4112344.04/1000}, 
      {yr: 2018, ghg: 4031523.96/1000},  
      {yr: 2030, ghg: props.sum/1000},
      {yr: 2050, ghg: 0}
    ]
  return (
      <div className="display-container">
        <h4>EU emmissions</h4>
        <div className="data-cards-container">
          <DataCard title="Emissions in 1990" value={GHG1990} unit={kTCO2e}/>
          <DataCard title="Projected emissions" value={props.sum} unit={kTCO2e}/>
          <DataCard title="Projected reduction" value={`${-1*calcReduction(GHG1990, props.sum)}%`} />
        </div>
        <ResponsiveContainer width="90%" height={300} >
        <LineChart data={data}>
          <Line type="monotone" dataKey="ghg" stroke="#181818" />
          <XAxis dataKey="yr" type="number" domain={['dataMin', 'dataMax']}/>
          <YAxis />
          <Tooltip />
        </LineChart>
        </ResponsiveContainer>
      </div>
  )
}
const mapStateToProps = (state) => {
    return {
      sum: state.projectedSum,
      countries: state.countriesProjectedGHGE,
      store: state
  }
  }
  
  const mapDispatchToProps = (dispatch) => {
    return {
      changeSum: (data) => dispatch({ type: 'CALC_SUM', data: data}),
  }
  }
  
  export default connect(mapStateToProps, mapDispatchToProps)(SumDisplay)