import * as React from "react"
import { countriesData as data } from "../data";
import InputRange from "./InputRange";


const InputContainer = () => {
  return (
      <div className="input-container">
        <h4>Reductions in countries of EU 27</h4>
        <form className="reductions-form">
            {Object.entries(data).map(([key, val], i)=><InputRange key={i} stateID={key} {...val}/>)}
        </form>
      </div>
  )
}

export default InputContainer
